<template>
	<view class="eva-section">
		<view class="eva-box" v-for="(v,k) in list" :key="k" @click.stop="toMessageDetails(v.id)">
			<view class="eva-top-left">
				<image class="img" :src="v.memberAvatar" mode="aspectFill"></image>
			</view>
			<view class="eva-content">
				<view class="eva-info uni-flex d-between d-items-center">
					<view class="">
						{{v.memberName}} <text class="top" v-if="v.sticky==1">置顶</text>
					</view>
					<view class="">
					</view>
				</view>
				<view class="uni-flex d-between d-items-center pad">
					<view class="">
						<text class="link-text title-nav">{{v.categoryName}}</text><text class="link-text title-nav">{{v.shopName}}</text>
					</view>
					<view class="uni-flex d-items-center link-text" @click.stop="tellOther(v.phone)">
						<image src="/static/img/kefurexian.png" mode="" class="phone-img"></image>
						联系他
					</view>
				</view>
				<view class="con uni-two-cut">{{v.content}}</view>
				<view class="uni-flex d-items-center d-between pad">
					<view class="time">{{v.publishDate}}</view>
					<view class="time uni-flex d-items-center">
					</view>
				</view>
				<view class="eva-img-con" v-if="v.image.length!=0">
					<image class="img" v-for=" (m,n) in v.image" :key="n" :src="m" mode="scaleToFill"></image>
				</view>
				<view class="link-con uni-flex d-between">
					<view class="uni-flex d-items-center">
						<image src="/static/img/yulan.png" mode="" class="link-img"></image>
						<view class="link-num">
							{{v.viewCount}}浏览
						</view>
					</view>
					<view class="uni-flex d-items-center" @click.stop="addGoode(v.id,k)">
						<image src="/static/img/dianzan.png" mode="" class="link-img"></image>
						<view class="link-num">
							{{v.likeCount}}点赞
						</view>
					</view>
					<view class="uni-flex d-items-center" v-if="uid!=v.memberId" @click.stop="gotoSendMessage(v)">
						<image src="/static/img/pinlun.png" mode="" class="link-img"></image>
						<view class="link-num">
							私信他
						</view>
					</view>
					<view class="uni-flex d-items-center" v-else>

					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		props: {
			list: {
				type: Array,
				dafault: []
			},
			type: [Number, String],
			uid:{
				type:String,
				default:''
			}
		},
		methods: {
			toMessageDetails(id) {
				this.$api.navTo(`/pages/goodDetails/senddetails?id=${id}`);
			},
			gotoSendMessage(e) {
				console.log(e)
				if(this.uid==''){
					this.$api.getCode(getApp().globalData.inverteUid);
				}else{
					let obj = {
						sticky: e.sticky,
						categoryName: e.categoryName,
						name: e.memberName,
						icon: e.memberAvatar,
						shopName:e.shopName,
						publishDate: e.publishDate
					}
					this.$api.navTo(
						`/pages/Previewindo/sendmessage?id=${e.id}&anoid=${e.memberId}&type=1&info=${encodeURIComponent(JSON.stringify(obj))}`)
				}
				
			},
			tellOther(phone) {
				this.$api.callPhone(phone)
			},
			addGoode(id, k) {
				if(this.uid==''){
					this.$api.getCode(getApp().globalData.inverteUid);
				}else{
					let parmas = {
						mid: this.uid,
						infoid: id
					}
					this._reqw
						.ipost(parmas, 'informationLike')
						.then(res => {
							console.log(res);
							res.result == 0 ? this.$emit('click', {
								ind: k,
								type: res.liked
							}) : this.$api.tip(res.resultNote)
						})
						.catch(err => {});
				}
				
			}
		}
	};
</script>

<style scoped lang="scss">
	.error-img {
		width: 30upx;
		height: 30upx;
	}

	.eva-item {
		margin-bottom: 20upx;
	}

	.title-nav {
		padding: 8upx 20upx;
		background: rgba(245, 245, 245, 1);
		border-radius: 6upx;
		margin-right: 20upx;
	}

	.link-text {
		font-size: 26upx;
	}

	.phone-img {
		width: 35upx;
		height: 42upx;
		margin-right: 6upx;
	}

	.link-con {
		padding: 20upx 0;
	}

	.link-img {
		width: 34upx;
		height: 34upx;
		margin-right: 6upx;
	}

	.link-num {
		font-size: 22upx;
		color: rgba(51, 51, 51, 1);
	}

	.uni-icon-location-filled {
		font-size: 32upx;
		margin-right: 10upx;
		color: #4CBC37;
	}

	.pad {
		padding: 10upx 0;
	}

	.top {
		padding: 0 10upx;
		background: rgba(224, 152, 76, 1);
		border-radius: 6upx;
		font-size: 24upx;
		color: rgba(255, 255, 255, 1);
		margin-left: 20upx;
	}

	.eva-active {
		padding: 20upx;
		border-radius: 8upx;
		background-color: #f8f8f8;

	}

	/* 评价 */
	.eva-section {
		display: flex;
		flex-direction: column;
		background-color: #F8F8F8;
		margin-bottom: 16upx;

		.e-header {
			display: flex;
			// align-items: center;
			height: 70upx;

			.tit {
				margin-right: 4upx;
			}

			.tip {
				flex: 1;
				text-align: right;
				color: #666666;
			}

			.icon-you {
				margin-left: 10upx;
			}
		}
	}

	.eva-box {
		background-color: #FFFFFF;
		padding: 30upx;
		display: flex;
		margin-bottom: 15upx;

		.eva-top-left {
			display: flex;

			.img {
				width: 120upx;
				height: 120upx;
				border-radius: 50%;
				margin-right: 20upx;
			}




		}

		.eva-content {
			margin: 20upx 0;
			flex: 1;

			.eva-info {
				font-size: 30upx;
				font-weight: bold;
				color: rgba(51, 51, 51, 1);
			}

			.time {
				color: #666666;
				font-size: 26upx;
			}

			.con {
				background-color: #F6F6F6;
				padding: 10upx;
				font-size: 24upx;
			}

			.eva-img-con {
				display: flex;
				align-items: center;
				padding: 20upx 0;

				.img {
					width: 200upx;
					height: 200upx;
					margin-right: 20upx;
				}
			}
		}
	}
</style>
